.testimoniales
{
    display: flex;
    flex-direction: column;
    align-items: center;
    @include desktop 
    {
        @include grid(5, 4rem);
        align-items: center;

        .imagen
        {
            grid-column: 1 / 3;
        }

        .contenido
        {
            grid-column: 3 / 6;
        }
    }

    h2
    {
        @include headingBorder($secundario, $primario);
    }
}

.contenido
{

    .testimonial
    {
        background-color: $primario;
        border: .5rem solid $secundario;
        margin-bottom: 4rem;
        padding: 2rem;
        border-radius: 1rem;
        position: relative;

        @include desktop 
        {
            max-width: 70%;

            &:nth-child(2)
            {
                margin-left: 10%;
            }

            &:nth-child(3)
            {
                margin-left: 20%;
            }

            &:nth-child(4)
            {
                margin-left: 30%;
            }
        }

        &::after
        {
            content: '';
            display: block;
            width: 5rem;
            height: 5rem;
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            background-position: center;
            position: absolute;
            top: 2rem;

        }

        &:last-of-type
        {
            margin-bottom: 0;
        }

        p
        {
            margin: 0;
            padding-left: 6rem;
        }
    }
}