.site-header
{
    @include bgImage('../img/header.webp', $secundarioOpacity);
    padding: 5rem 0;

    .logo
    {
        max-width: 20rem;
        margin: 0 auto;

        @include tablet 
        {
            margin: 0;
        }
        
    }

    .contenido-header
    {
        padding: 10rem 0;
        @include desktop 
        {
            padding: 15rem 0;
        }

        h1
        {
            color: $blanco;
            font-size: 4.5rem;

            @include tablet 
            {
                font-size: 6rem;
                max-width: 50%;
            }

            @include desktop 
            {
                max-width: 40%;
            }
        }
    }
}
