.planes
{
    background-color: $secundario;

    h2
    {
        @include headingBorder($blanco, $primario);
    }

    .descripcion
    {
        color: $blanco;
    }
}

.nuestros-planes
{
    display: flex;
    flex-direction: column;

    @include desktop 
    {
        flex-direction: row;
        max-width: 80rem;
        margin: 10rem auto 0 auto;
    }

    .plan
    {
        flex-grow: 1;
        background-color: $blanco;
        padding: 16rem 4rem 4rem 4rem;
        border-bottom: 2px solid #e1e1e1;

        @include desktop 
        {
            border-bottom: none;
            transition-property: transform;
            transition-duration: .3s;

            &:hover
            {
                transform: scale(1.1);
            }
        }

        .precio
        {
            font-size: 1.8rem;
            text-align: center;
            font-weight: 700;
            text-transform: uppercase
        }

        .listado
        {
            li
            {
                text-align: center;
                margin-bottom: 2rem;

                &:last-of-type
                {
                    margin-bottom: 0;
                }
            }
        }

        &.inicio
        {
            @include iconoBg('../img/icono_inicio.svg');
            z-index: 0;
        }

        &.negocio  
        {
            order: -1;
            background-color: $primario;
            @include iconoBg('../img/icono_negocio.svg');

            @include desktop 
            {
                order: unset;
                margin: -5rem 0;
                border-radius: 2rem;
                background-position: 50% 7rem;
                padding-top: calc(16rem + 5rem);
                
                z-index: 1;
            }
        }
        
        &.pro
        {
            @include iconoBg('../img/icono_pro.svg');
            z-index: 0;
        }
    }
}