.beneficios
{
    background-color: $primario;

    .grid-beneficios
    {
        
        @include tablet 
        {
            @include grid(5, 4rem);
            align-items: center;
        }
    }

    .contenido-beneficios
    {
        grid-column: 1 / 4;

        h2
        {
            @include headingBorder($secundario, $blanco);
        }
    }

    .imagen
    {
        grid-column: 4 / 6;
        transition-property: transform;
        transition-duration: .3s;

        &:hover
        {
            transform: scale(1.1) rotate(8deg);
        }
    }
}