.footer
{
    background-color: $secundario;
    color: $blanco;

    .footer-grid
    {
        padding: 5rem 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        
        border-bottom: 2px solid $primario;

        @include tablet 
        {
            @include grid(4, 4rem);
        }
    }

    .logo
    {
        width: 100%;
        margin: 0 auto 5rem auto;
        @include tablet 
        {
            max-width: 20rem;
            margin-bottom: 0;
        }
    }

    .footer-widget
    {
        margin-bottom: 2rem;

        @include tablet 
        {
            margin-bottom: 0;
        }

        h3
        {
            color: $primario;
            
            @include tablet 
            {
                text-align: left;
                margin-bottom: 3rem;
            }
        }
    }

    nav
    {
        a
        {
            display: block;
            color: $blanco;
            margin-bottom: 2rem;
            text-align: center;

            @include tablet 
            {
                text-align: left;
            }

            &:last-of-type
            {
                margin-bottom: 0;
            }
        }
    }

    .copyright
    {
        color: $blanco;
        text-align: center;
        margin: 0;
        padding-top: 1rem;
        text-transform: uppercase;
        font-size: 1.2rem;
    }
}